import { useState, FC, useEffect } from 'react';
import classNames from 'classnames';

export const Layout: FC = ({ children }) => {
  const [isDomRendered, setIsDomRendered] = useState();

  useEffect(() => {
    setIsDomRendered(true);
  }, []);

  return (
    <>
      <div
        id="wrapper"
        className={classNames(
          'main-container',
          isDomRendered ? 'main-container--ready' : null
        )}>
        {children}
      </div>
      {!isDomRendered && (
        <div className="pre-load">
          <img src="assets/bg-blur.jpg" />
        </div>
      )}
    </>
  );
};
